<template>
  <div>
    <!-- <div id="local_stream"></div> -->
  </div>
</template>

<script>
// import AgoraRTC from "AgoraRTC";
import config from "../common/config";

// AgoraRTC.Logger.setLogLevel(AgoraRTC.Logger.NONE);
export default {
  props: {
    mode: {
      type: String,
      default: "live",
    },
    codec: {
      type: String,
      default: "h264",
    },
  },
  data() {
    return {
      client: null,
    };
  },
  methods: {
    async init() {
      this.client = AgoraRTC.createClient({
        mode: this.mode,
        codec: this.codec,
      });
      this.client.on("error", (err) => {
        console.log("client error:", err);
      });
      this.client.on("stream-added", (evt) => {
        console.log("stream-added");
        this.$emit("added", evt);
      });
      this.client.on("stream-subscribed", (evt) => {
        console.log("stream-subscribed");
        this.$emit("subscribed", evt);
      });
      this.client.on("peer-online", (evt) => {
        console.log("peer-online", evt);
      });
      this.client.on("peer-leave", (evt) => {
        console.log("peer-leave", evt);
      });
      this.client.on("stream-removed", (evt) => {
        console.log("stream-removed");
        this.$emit("removed", evt);
      });
      this.client.on("error", (err) => {
        console.log(err);
      });
      return await this.client.init(config.appid);
    },
    async join(channel, uid, token) {
      return new Promise((resolve, reject) => {
        this.client.join(
          token,
          channel,
          uid,
          (streamid) => {
            resolve(streamid);
          },
          (error) => {
            reject(error);
          }
        );
      });
    },
    createLocalStream(streamID) {
      console.log("createLocalStream => ", streamID);
      return new Promise((resolve, reject) => {
        const localStream = AgoraRTC.createStream({
          streamID,
          audio: true,
          video: true,
          screen: false,
        });
        localStream.init(
          () => {
            resolve(localStream);
          },
          (error) => {
            reject(error);
          }
        );
      });
    },
    publish(localStream) {
      this.client.publish(localStream, (error) => {
        console.log("publish localstream =>", error);
      });
    },
    async subscribe(remoteStream) {
      this.client.subscribe(remoteStream, (err) => {
        console.log("subscribe:", err);
      });
    },
    leave() {
      if (this.client) {
        this.client.leave();
      }
    },
    async test() {
      this.client = AgoraRTC.createClient({
        mode: this.mode,
        codec: this.codec,
      });
      await this.client.init(config.appid);
      this.client.join(
        null,
        "sma2lbao_room",
        "sma2lbao",
        (streamID) => {
          console.log(streamID);
          const localStream = AgoraRTC.createStream({
            streamID,
            audio: true,
            video: true,
            screen: false,
          });
          localStream.init(
            async () => {
              console.log("play_local");
              localStream.play("local_stream", { fit: "cover" });
              console.log("play_local");
              await this.client.publish(localStream);
              console.log("----- publish localStream -----");
            },
            (error) => {
              console.error(error);
            }
          );
        },
        (error) => {
          console.log(error);
        }
      );
    },
    getDevices() {
      console.warn("RTC GetDevices => ", Date.now());
      return new Promise((resolve) => {
        AgoraRTC.getDevices((devices) => {
          console.warn("devices => ", devices);
          resolve(devices);
        });
      });
    },
    async getPermission() {
      /* eslint-disable */
      console.warn("RTC GetPermission => ", Date.now());
      let arg1 = { audio: true, video: false };
      let arg2 = { audio: false, video: true };
      let tempAudioStream = AgoraRTC.createStream(arg1);
      let tempVideoStream = AgoraRTC.createStream(arg2);
      /* eslint-disable */
    }
  },
  created() {
    this.getPermission();
    // let devices = await this.getDevices();
    // if (!devices || devices.length < 1) {
    //   this.$toast("No Camera");
    // }
    // let noPermission = true;
    // devices.forEach(item => {
    //   if (item.kind === "videoinput" && item.deviceId && item.deviceId.length > 0) {
    //     noPermission = false;
    //   }
    // });
    // if(noPermission) this.$toast("No Camera Permission");
  },
  mounted() {
    this.getDevices();
  },
};
</script>
